.img-film__poster {
  background: url("poster.fd8c97a1.jpg") center / cover;
  border-radius: 5px;
  display: block;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .img-film__poster {
    width: 240px;
    height: 357px;
