.modal-active {
  background: var(--main-accent-color);
  color: var(--background-color);
  box-shadow: 0px 0px 20px var(--main-accent-color);
  border: none;
  padding: 15px 10px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .modal-active {
    min-width: 110px;
    padding: 6px;
    font-size: 12px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .modal-active {
    min-width: 125.03px;
    padding: 6px;
    font-size: 12px;
  }
}

@media screen and (min-width: 1200px) {
  .modal-active {
    min-width: 136px;
    padding: 15px 10px 13px;
  }
}

.team-backdrop {
  z-index: 1;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  background-color: #00000040;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.team-modal {
  z-index: 2;
  width: 80%;
  height: 80%;
  background-color: var(--background-color);
  border-radius: 20px;
  padding: 45px 32px;
  position: relative;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .team-modal {
    width: 704px;
    gap: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .team-modal {
    width: 906px;
    max-height: 80%;
    gap: 10px;
  }
}

.icon-heart {
  margin-left: 10px;
  margin-right: 10px;
  animation: 3s infinite pulse;
}

.team-modal__title {
  text-align: center;
  background-color: var(--footer-text-color);
  background-image: linear-gradient(90deg, var(--main-accent-color), var(--footer-text-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 15px;
  font-size: 24px;
  font-weight: 700;
}

@media screen and (min-width: 1200px) {
  .team-modal__title {
    font-size: 28px;
  }
}

.team-modal__list {
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: stretch;
  gap: 20px;
  display: flex;
}

@media screen and (min-width: 1199px) {
  .team-modal__list {
    gap: 10px;
  }
}

.team-modal__item {
  border-radius: 8px;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  transition: all .3s;
  display: flex;
  box-shadow: 11px 8px 26px -9px #000000d1;
}

.team-modal__item:hover, .team-modal__item:focus {
  transform: scale(1.05);
  box-shadow: 0 10px 13px -7px #000, 4px 1px 14px 5px #0000;
}

.team-modal__foto {
  max-width: 90%;
  height: auto;
  object-fit: cover;
  border-radius: 10px;
  display: block;
  overflow: hidden;
}

@media screen and (max-width: 480px) {
  .team-modal__foto {
    width: 90%;
  }
}

.team-modal__info {
  text-align: center;
  padding: 10px;
}

.team-modal__name {
  color: var(--footer-text-color);
  margin-bottom: 7px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.14;
}

.team-modal__role {
  color: var(--main-accent-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.team-modal__soclist {
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 15px;
  padding-top: 10px;
  display: flex;
}

.team-modal__soclink {
  width: 30px;
  height: 30px;
  fill: var(--footer-text-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), fill .25s cubic-bezier(.4, 0, .2, 1), transform .5s linear;
  display: inline-flex;
}

.team-modal__soclink:hover, .team-modal__soclink:focus {
  background-color: var(--main-accent-color);
  fill: var(--background-color);
  transform: scale(1.3);
}

button {
  cursor: pointer;
}

.enter-btn {
  margin-left: auto;
}

.registrationWrapper {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background-color: #0009;
  transition: all .25s ease-in-out;
  position: absolute;
  top: 0;
  overflow: hidden;
}

.visible {
  opacity: 1;
  visibility: visible;
  overflow: hidden;
}

.registr-form {
  width: 320px;
  background-color: #212121;
  border-radius: 20px;
  flex-direction: column;
  margin: 0 auto;
  padding: 20px;
  transition: all .4s ease-in-out;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.registr-form.hidden {
  transition: all .4s ease-in-out;
  left: -100%;
}

.form-title {
  text-align: center;
  color: var(--text-color-white);
  letter-spacing: .2em;
  margin-bottom: 40px;
  font-size: 30px;
  font-weight: 600;
}

.input:focus ~ .pass-email-span, .input:valid ~ .pass-email-span {
  color: var(--text-color-white);
  padding: 0 10 px;
  border-left: 1px solid var(--main-accent-color);
  border-right: 1px solid var(--main-accent-color);
  letter-spacing: .2em;
  background: #212121;
  font-size: .65em;
  transform: translateX(10px)translateY(-7px);
}

.input:focus, .input:valid {
  border: 1px solid var(--main-accent-color);
}

.pass-email-span {
  pointer-events: none;
  color: var(--text-color-white);
  text-transform: uppercase;
  padding: 10px;
  font-size: 1em;
  transition: all .25s;
  position: absolute;
  left: 20px;
}

.first-name, .email {
  width: 100%;
  background: inherit;
  color: var(--text-color-white);
  resize: none;
  border: 1px solid #ffffff40;
  border-radius: 5px;
  outline: none;
  margin-bottom: 30px;
  padding: 10px;
  font-size: 3em;
  transition: all .25s;
}

.registr-btn, .login-btn {
  color: #fff;
  cursor: pointer;
  background: #ffffff40;
  border: none;
  border-radius: 4px;
  outline: none;
  margin: 0 auto 40px;
  padding: 10px 50px;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.registr-btn:hover, .login-btn:hover {
  background: #60606040;
}

.registr-text {
  font-size: 16px;
}

.changeForm__text {
  text-align: center;
  color: var(--text-color-white);
  font-size: 18px;
}

.changeForm__span {
  cursor: pointer;
  font-size: 28px;
  transition: all .25s ease-in-out;
}

.changeForm__span:hover, .changeForm__span:focus {
  color: var(--main-accent-color);
  transition: all .25s ease-in-out;
}

.login-form {
  width: 320px;
  background-color: #212121;
  border-radius: 20px;
  flex-direction: column;
  margin: 0 auto;
  padding: 20px;
  transition: all .25s ease-in-out;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.registr-form.registr-form.hidden + .login-form.visible {
  transition: all .25s ease-in-out;
  left: 50%;
}

.loginedModal {
  width: 320px;
  background-color: #212121;
  border-radius: 20px;
  margin: 0 auto;
  padding: 20px;
  transition: all .25s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loginedWrapper {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background-color: #0009;
  transition: all .25s ease-in-out;
  position: absolute;
  top: 0;
  overflow: hidden;
}

.loginedWrapper.active {
  opacity: 1;
  visibility: visible;
}

.exitBtnForm {
  width: 180px;
